html {
  font-family: 'Maven Pro', sans-serif;
}

body {
}

.page-content {
  padding-bottom: 6rem;
}

section#brand {
  position: relative;
  text-align: center;
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
  height: 100vh;
  background: url('/uploads/3-rings.jpg') no-repeat;
  background-position: bottom;
  background-size: contain;
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#877650+0,ffffff+50,877650+100&0.2+0,0.2+100 */
  background: -moz-linear-gradient(-45deg,  rgba(135,118,80,0.2) 0%, rgba(255,255,255,0.2) 50%, rgba(135,118,80,0.2) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg,  rgba(135,118,80,0.2) 0%,rgba(255,255,255,0.2) 50%,rgba(135,118,80,0.2) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg,  rgba(135,118,80,0.2) 0%,rgba(255,255,255,0.2) 50%,rgba(135,118,80,0.2) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#33877650', endColorstr='#33877650',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */

}



section#arrow {
  text-align: center;
  margin-top: -8rem;
  i {
    margin-left: -.5rem;
    color: $gold;
    font-size: 4rem;
    animation: throb 2s infinite;
  }
}

@keyframes throb {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  50% {
    -webkit-transform: translate(0, 10px);
            transform: translate(0, 10px); }
  100% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); } }

