.grid-container {
  // @include grid-visual();
  max-width: 1200px;
  margin: 0 auto;
  @include grid-container;
  .grid-column {
    @include grid-column(12);
  }
}

.logo-wrapper {
  text-align: center;
  margin-bottom: 4rem;
}

.logo {
  width: 50vw;
  position: relative;
  z-index: 1;
}

h1.intro {
  font-weight: normal;
  text-transform: uppercase;
  color: $gold;
  letter-spacing: 1px;
  display: inline-block;
  margin-top: 1em;
  margin-bottom: .2em
}

h2.intro {
  font-weight: normal;
  color: $gold;
  letter-spacing: 1px;
  margin: 0;
}

section#about {
  margin-top: 10rem;
  .grid-column {
    // border: 1px solid rgba(150,150,150,0.6);
    box-shadow: 3px 3px 18px 0px rgba(150,150,150,0.8);
  }
  .image {
    height: 700px;
    background: url('/uploads/about.jpg') no-repeat;
    background-size: cover;
    position: relative;
    .info {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      width: 50%;
      background-color: rgba(255,255,255,0.8);
    }
    .info-wrapper {
      color: $gray;
      padding: 2rem;
      h2.headline {
        font-size: 65px;
        margin: 0;
        font-weight: 500;
        line-height: 1;
        margin-top: 0;
        margin-bottom: 1rem;
        color: $gold;
      }
      h3.subhead {
        font-weight: 500;
        font-size: 20px;
        line-height: 1.4;
        margin-top: 0;
        margin-bottom: 1rem;
      }
      p {
        font-size: 18px;
        line-height: 1.6;
        margin-bottom: 1rem;
      }
    }
  }
}

